import { MoonLoader } from "react-spinners";

function FullPageSpinner() {
  return (
    <div className="w-[100dvw] h-[100dvh] flex justify-center items-center bg-[#2D3439]">
      <MoonLoader color="#22C55E" size="150px" speedMultiplier={0.5} />
    </div>
  );
}

export default FullPageSpinner;
